<template>
    <div class="respsow-container">
        <h1>{{$route.meta.title}}</h1>
        <input type="button" value="제출" @click="btnSrvSubmit">
        <input type="button" value="뒤로가기" @click="goBack">
        <div class="head-div">
            <ul>
                <li><input type="text" :value="data.srvMstr.srvMstNm" placeholder="설문지명"  readonly/></li>
                <li><input type="text" :value="data.srvMstr.srvMstDc" placeholder="설문지설명" readonly/></li>
                <li><input type="text" :value="data.srvMstr.srvMstTg" placeholder="설문지대상" readonly/></li>
            </ul>
        </div>
        <div class="content-div">
            <h3>설문지 영역</h3>
            <div class="render-workspace" v-for="(item, index) in data.srvMtritems" :key="index">
                <ResponseSpace :vo="item" :index="index"/>
            </div>
        </div>
    </div>
</template>

<script>
import ResponseSpace from '@/components/survey/response/space/ResponseSpace'
export default {
  name: 'ResponseSpaceContainer',
  data () {
    return {
      url: '/action/response/',
      param: {value: this.$route.params.srvMstrId},
      data: {
        srvMstr: {
          srvMstNm: '',
          srvMstDc: '',
          srvMstTg: '',
          pbshDt: '',
          exprDt: ''
        },
        srvMtritems: []
      },
      respData: {
        array: []
      }
    }
  },
  components: {
    ResponseSpace
  },
  created () {
    this.init()
    this.$eventBus.$on('tr_inputRespVal', this.event_inputRespVal)
  },
  methods: {
    init () {
      this.axios.post('/action/management/detail', this.param).then((response) => {
        this.data = JSON.parse(response.data)
      })
    },
    btnSrvSubmit () {
      if (confirm('제출하시겠습니까?')) {
        this.axios.post(this.url + 'srvSubmit', {value: this.respData}).then((response) => {
          alert('제출이 완료되었습니다.')
          this.$router.go(-1)
        })
      }
    },
    event_inputRespVal (respVal, index) {
      this.respData.array.splice(index, 1, respVal)
    },
    // 뒤로가기
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  .respsow-container {
    padding: 10px;
    width: 45em;
    height: 100%;
    margin: auto;
  }
  .respsow-container div.head-div {
    margin: 30px 0px 30px 0px;
    width: 40em;
    border: 1px solid;
  }
  .respsow-container div.content-div {
    margin: 30px 0px 30px 0px;
    width: 40em;
  }
  .respsow-container div ul {
    list-style: none;
    padding: 5px;
  }
  .respsow-container div ul li {
    padding: 10px;
  }

  .respsow-container div ul li input {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid;
    padding: 10px;
    width: 30em;
  }
</style>



