<template>
  <div class="response-space">
      <ul>
        <li>
          <input type="text" :value="vo.srvMtritemsTitle" placeholder="질문"/>
        </li>
        <li>
          <AnswerLoader :vo="srvMtritem" :index="index"/>
        </li>
      </ul>
  </div>
</template>

<script>
import AnswerLoader from '@/components/survey/response/space/answer/AnswerLoader'
export default {
  name: 'ResponseSpace',
  props: ['index', 'vo'],
  data () {
    return {
      srvMtritem: {
        srvMstrId: '',
        srvMtritemsTitle: '',
        srvMtritemsType: '',
        srvItems: []
      }
    }
  },
  components: {
    AnswerLoader
  },
  created () {
    this.srvMtritem = this.vo
  },
  methods: {
  }
}
</script>

<style scoped>
  div.response-space {
    border: 1px solid;
    width: 40em;
    height: auto;
    margin: 10px 0px 10px 0px;
  }
  div.response-space ul {
    list-style: none;
    padding: 30px;
  }
  div.response-space ul li input {
    width: 40em;
    padding: 10px;
    margin: 0px;
  }
   div.response-space ul li select {
    width: 7.5em;
    padding: 10px;
    vertical-align: middle;
  }
</style>



