<template>
    <component :vo="this.vo" :index="index" :is="component"></component>
</template>

<script>
export default {
  name: 'AnswerLoader',
  data () {
    return {
      component: null
    }
  },
  props: {
    vo: {required: true, default: null},
    index: {required: true}
  },
  computed: {
    loadAnswer () {
      const component = String(this.vo.srvMtritemsType) === '0' ? 'SelectAnswer' : 'TextAnswer'
      return () => import(`@/components/survey/response/space/answer/${component}.vue`)
    }
  },
  created () {
    this.component = () => this.loadAnswer()
  }
}
</script>

<style scoped>
</style>



